*, :before, :after {
  box-sizing: border-box;
  border: 0 solid #e5e7eb;
}

body {
  color: #fff;
  min-height: 100vh;
  line-height: inherit;
  background-color: #111827;
  flex-direction: column;
  margin: 0;
  display: flex;
}

header {
  backdrop-filter: blur(12px);
  z-index: 50;
  margin-top: .75rem;
  margin-left: .75rem;
  padding: 1rem;
  position: sticky;
  top: 0;
}

header h1 {
  font-size: 1.5rem;
  font-weight: 700;
  line-height: 2rem;
}

header span {
  color: #f472b6;
  font-size: .875rem;
  line-height: 1.25rem;
}

header img {
  width: 2.5rem;
  height: 2.5rem;
  margin-right: .5rem;
}

button {
  color: #fff;
  cursor: pointer;
  border-radius: .375rem;
  padding: .75rem 2.5rem;
  font-size: .875rem;
  line-height: 1.25rem;
}

#moduleOnButton {
  background-color: #2563eb;
  margin-right: .5rem;
}

#moduleOnButton:hover {
  background-color: #3b82f6;
}

#moduleOffButton {
  background-color: #dc2626;
}

#moduleOffButton:hover {
  background-color: #ef4444;
}

input[type="range"]::-webkit-slider-thumb {
  appearance: none;
  cursor: pointer;
  background: #f472b6;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  margin-top: -6px;
}

input[type="range"]::-moz-range-thumb {
  cursor: pointer;
  background: #f472b6;
  border-radius: 50%;
  width: 20px;
  height: 20px;
}

input[type="range"]::-webkit-slider-runnable-track {
  cursor: pointer;
  background: #4b5563;
  border-radius: 5px;
  width: 100%;
  height: 8px;
}

input[type="range"]::-moz-range-track {
  cursor: pointer;
  background: #4b5563;
  border-radius: 5px;
  width: 100%;
  height: 8px;
}

footer {
  text-align: center;
  color: #6b7280;
  margin-top: auto;
  margin-bottom: .5rem;
  padding-top: .75rem;
  padding-bottom: .75rem;
  font-size: .75rem;
  line-height: 1rem;
}
